import { createSlice } from '@reduxjs/toolkit';


const initialState = {
    data: {},
};


export const columnPreferenceSlice = createSlice({
  name: 'columnPreference',
  initialState: initialState,
  reducers: {
    initAllColumnPreference(state, action) {
      state.data = action.payload;
    },
    setColumnPreference: (state, action) => {
        const { name, ...rest } = action.payload;
        if (!name) return;
        state.data[name] = rest;
    },
    updateColumnPreference: (state, action) => {
        const { name, ...rest } = action.payload;
        if (!name) return;
        state.data[name] = {...(state.data[name] || {}), ...rest};
      },
  },
});

export const { setColumnPreference, initAllColumnPreference, updateColumnPreference } = columnPreferenceSlice.actions;

