import { createDataSlice, initialState } from './dataSlice';
import { sqlElements } from '@/utils/sql/sql';
import { runQuery } from '@/utils/api';
import _ from 'lodash';
import { padItems } from '@/utils/histogramBins';
import { sanitizeName } from '@/utils/sql/helpers';
import { isMissing } from '@/utils/helpers';

export const histogramSlice = createDataSlice('histogram');


export const fetchHistogramData = ({histogramColumn, bin}) => async (dispatch, getState) => {
  const { data, loading, shouldRefresh } = getState().histogram || initialState;
  if ((data || loading) && !shouldRefresh) return; // ðŸ‘ˆ Already loading or loaded â€” skip!

  const {histogramBreakdownColumn} = getState().settings;

  dispatch(histogramSlice.actions.fetchStart());

  try {
    const { min, step, round_to } = bin;
    const elements = sqlElements(getState());
    const query = `
   WITH filtered AS (
      SELECT 
        ${sanitizeName(histogramColumn)} AS value, 
        ${sanitizeName(histogramBreakdownColumn)} AS breakdown
      ${elements.from}
      ${elements.where} AND value IS NOT NULL  
    )
    SELECT breakdown, FLOOR((value - ${min}) / ${step}) AS binIdx, 
        COUNT(*) AS cnt
    FROM filtered
    GROUP BY 1, 2
    ORDER BY 1, 2
 `;
    const data = await runQuery({dispatch, key: 'histogram', query: query});

    const groupped = _(data).groupBy('breakdown').toPairs().map(([breakdown, items]) => {
      const paddedItems = padItems({min, step, round_to, cntValues: items});
      const x = _.map(paddedItems, 'value');
      const y = _.map(paddedItems, 'cnt');
      const customdata = _.map(paddedItems, item => {
        const condCol = [`${sanitizeName(histogramColumn)} >= ${item.binMin}`, `${sanitizeName(histogramColumn)} < ${item.binMax}`];
        const condBreakdown = isMissing(histogramBreakdownColumn) ? [] : [`${sanitizeName(histogramBreakdownColumn)} = '${breakdown}'`];
        const condExpr = [...condCol, ...condBreakdown].join(' AND ');
        return {condExpr};
      });
      return {name: breakdown, x, y, customdata};
    }).value();


    dispatch(histogramSlice.actions.fetchSuccess(groupped));
  } catch (err) {
    console.error(err);
    dispatch(histogramSlice.actions.fetchError(err.message));
  }
};