import _ from 'lodash';

/**
 * Checks if a value is null or undefined
 */
export const isMissing = (value) => {
    if (value === null || value === undefined) {
        return true;
    } else {
        return false;
    }
}

/**
 * Returns the first non-null/undefined value from the provided arguments
 */
export const coalesce = (...args) => {
    if (args.length > 0) {
        const found = args.find(value => !isMissing(value));
        return isMissing(found) ? null : found;
    } else {
        throw new Error('No arguments provided to coalesce');
    }
}



/**
 * Loads a JSON value from local storage
 * @param {string} key - The key to load the value from
 * @returns {any} The loaded JSON value, or null if no value is found
 */
export const loadLocalJsonStorage = (key) => {
    const value = loadLocalStorage(key);
    if (value) {
        return JSON.parse(value);
    } else {
        return null;
    }
}

export const loadLocalStorage = (key) => {
    if (typeof window !== 'undefined') {
        return localStorage.getItem(key);
    } else {
        return null;
    }
}

export const setEqual = (a, b) => {
    return _.isEqual(_.sortBy(a), _.sortBy(b));
}

