import _ from 'lodash';
import { parseFilterItem } from './parseFilterItem';
import { sanitizeName } from './helpers';

export const toSqlCondition = (filters) => {
    const sqlList = _.map(filters, (filterItem, columnName) => {
      try {
        if (filterItem === null) {
          return null;
        }
        const sql = parseFilterItem(columnName, filterItem);
        return sql;
      } catch (error) {
        console.error(`Error parsing filter for column ${columnName}:`, error);
        return null;
      }
    });

    const expr = sqlList.filter(Boolean).map(sql => `(${sql})`).join(' AND ');
    return expr;
  }

export const constructSqlCondition = ({filters, baselineFilters}) => {
  const filtersSql = [toSqlCondition(filters), toSqlCondition(baselineFilters)].filter(Boolean).join(' AND ');
  return filtersSql || 'True';
}

export const sqlElements = (state) => {
  const { tableExpr } = state.dataFile;
  const { filters, baselineFilters } = state.agColDefs;
  const { pageSize, pageNumber } = state.viewing.pagination;
  const { sorting } = state.agColDefs;

  const order = sorting.length === 0 ? '' : `ORDER BY ${sorting.map(s => `${sanitizeName(s.field)} ${s.order.toUpperCase()}`).join(', ')}`;
  return {
    from: `FROM ${tableExpr}`,
    where: `WHERE ${constructSqlCondition({filters, baselineFilters})}`,
    order,
    pagination: `LIMIT ${pageSize} OFFSET ${pageNumber * pageSize}`,
  };
}

