import { createDataSlice } from './dataSlice';
import { sqlElements } from '@/utils/sql/sql';
import { runQuery } from '@/utils/api';
import { sanitizeName } from '@/utils/sql/helpers';
import _ from 'lodash';
import { samplePkListExpr } from '@/utils/sql/helpers';

export const Y_LABEL_VALUE = '__y__label__value__'

export const heatMapSlice = createDataSlice('heatMap');

const computeStats = (data) => {
  return {min: _.min(data), max: _.max(data), sum: _.sum(data)};
}

const pivotData = (data) => {
  const xLabels = _(data).map('x').uniq().map(String).value();

  const pivotedData = _(data).groupBy('y').map((rows, y) => {
      const row = _(rows).map(row => [row.x, row]).fromPairs().value();
      return {...row, [Y_LABEL_VALUE]: y};
  }).value();

  const allStats = computeStats(_.map(data, 'cnt'));


  // Compute max value for each y label
  const yStats = _.fromPairs(_.map(pivotedData, (row, i) => {
    const subData = _(row).values().map('cnt').value();
    return [row[Y_LABEL_VALUE], computeStats(subData)];
  }));

  const xStats = _.fromPairs(_.map(xLabels, (xLabel, i) => {
    const subData = _.map(pivotedData, (row) => row[xLabel]?.cnt);
    return [xLabel, computeStats(subData)];
  }));

  const rowData = _.map(pivotedData, (row) => {
    return {
      rowTotal: yStats[row[Y_LABEL_VALUE]].sum,
      ...row,
    };
  });
  return {xLabels, rowData, xStats, yStats, allStats};
}


export const fetchHeatMap = ({heatmapXColumn, heatmapYColumn}) => async (dispatch, getState) => {
  const { data, loading, shouldRefresh } = getState().heatMap || { data: null, loading: false, shouldRefresh: false };

  if ((data || loading) && !shouldRefresh) return; // ðŸ‘ˆ Already loading or loaded â€” skip!

  dispatch(heatMapSlice.actions.fetchStart());

  try {
    const elements = sqlElements(getState());
    const query = `
    WITH filtered AS (
          SELECT 
            ${sanitizeName(heatmapXColumn)} AS x, 
            ${sanitizeName(heatmapYColumn)} AS y
          ${elements.from}
          ${elements.where}
      ) SELECT x, y, COUNT(*) AS cnt 
       FROM filtered
       GROUP BY x, y
      `;
    const data = await runQuery({dispatch, key: 'heatMap', query: query});
    const heatMap = pivotData(data);
    dispatch(heatMapSlice.actions.fetchSuccess(heatMap));
  } catch (err) {
    console.error(err);
    dispatch(heatMapSlice.actions.fetchError(err.message));
  }
};