import { createDataSlice, initialState } from './dataSlice';
import { sqlElements } from '@/utils/sql/sql';
import { runQuery } from '@/utils/api';
import { sanitizeName } from '@/utils/sql/helpers';

export const boxPlotSlice = createDataSlice('boxPlot');



export const fetchBoxPlotData = ({boxPlotColumns}) => async (dispatch, getState) => {
  const { data, loading, shouldRefresh } = getState().boxPlot || initialState;
  const { boxPlotBreakdownColumn } = getState().settings;

  if ((data || loading) && !shouldRefresh) return; // ðŸ‘ˆ Already loading or loaded â€” skip!


  dispatch(boxPlotSlice.actions.fetchStart());

  try {

    const boxExpr = (c) => {
      const e = sanitizeName(c);
      return `{
        'min' : MIN(${e}),
        'max' : MAX(${e}),
        'std' : STDDEV_POP(${e}),
        'skewness': SKEWNESS(${e}),
        'avg' : AVG(${e}),
        'q25' : QUANTILE_CONT(${e}, 0.25),
        'q50' : QUANTILE_CONT(${e}, 0.5),
        'q75' : QUANTILE_CONT(${e}, 0.75)
      } AS ${e}`;
      };
    const elements = sqlElements(getState());
    const query = `
    WITH filtered AS (
          SELECT ${sanitizeName(boxPlotBreakdownColumn)} AS breakdown, 
          ${boxPlotColumns.map(sanitizeName).join(', ')}
          ${elements.from}
          ${elements.where}
      ) SELECT breakdown, COUNT(*) AS count, ${boxPlotColumns.map(c => boxExpr(c)).join(', ')}
      FROM filtered
      GROUP BY breakdown
    `;
    const data = await runQuery({dispatch, key: 'boxPlot', query: query});

    dispatch(boxPlotSlice.actions.fetchSuccess(data));
  } catch (err) {
    console.error(err);
    dispatch(boxPlotSlice.actions.fetchError(err.message));
  }
};