
export const API_PREFIX = './api';

export const getScheme = (url) => {
  if (!url) {
    return '';
  } else if (url.includes('://')) {
    return url.split('://')[0].toLowerCase();
  } else {
    return '';
  }
}

export const needProxy = (url) => {
  const scheme = getScheme(url);
  return !['http', 'https', ''].includes(scheme);
}

export const proxyedUrl = (url) => {
  if (!needProxy(url)) {
    return url;
  } else {
    return `${API_PREFIX}/s3-proxy?url=${encodeURIComponent(url)}`;
  }
}

export const getFileUrl = (fullPath) => {
  return `${API_PREFIX}/get-file?path=${encodeURIComponent(fullPath)}`;
}

