import { createDataSlice, initialState } from './dataSlice';
import { sqlElements } from '@/utils/sql/sql';
import { runQuery } from '@/utils/api';
import { sanitizeName } from '@/utils/sql/helpers';

export const bubblePlotSlice = createDataSlice('bubblePlot');



export const fetchBubblePlotData = ({bubblePlotXColumn, bubblePlotYColumn, xBin, yBin}) => async (dispatch, getState) => {
  const { data, loading, shouldRefresh } = getState().bubblePlot || initialState;
  const bubblePlotBreakdownColumn = getState().settings.bubblePlotBreakdownColumn;
  if ((data || loading) && !shouldRefresh) return; // ðŸ‘ˆ Already loading or loaded â€” skip!

  dispatch(bubblePlotSlice.actions.fetchStart());

  try {
    const elements = sqlElements(getState());
    const query = `
    WITH filtered AS (
          SELECT 
            ${sanitizeName(bubblePlotXColumn)} AS x,
            ${sanitizeName(bubblePlotYColumn)} AS y,
            ${sanitizeName(bubblePlotBreakdownColumn)} AS breakdown
          ${elements.from}
          ${elements.where} AND x IS NOT NULL AND y IS NOT NULL
      ), binned AS (
      SELECT
          p.*,
          FLOOR((p.x - ${xBin.min}) / ${xBin.step})::INT AS bin_x,
          FLOOR((p.y - ${yBin.min}) / ${yBin.step})::INT AS bin_y
      FROM filtered AS p
      )
      SELECT breakdown, bin_x, bin_y,
      -- Compute the â€œbubbleâ€ center as the average x and y within that bin
      AVG(x)            AS x,
      AVG(y)            AS y,
      COUNT(*)          AS count
      FROM binned
      GROUP BY 1, 2, 3
    `;
    const data = await runQuery({dispatch, key: 'bubblePlot', query: query});
    const grouped = _(data).groupBy('breakdown').toPairs().map(([breakdown, items]) => {
      const x = _.map(items, 'x');
      const y = _.map(items, 'y');
      const customdata = _.map(items, item => {
        const xMin = item.bin_x * xBin.step + xBin.min;
        const xMax = xMin + xBin.step;
        const yMin = item.bin_y * yBin.step + yBin.min;
        const yMax = yMin + yBin.step;
        const xCol = sanitizeName(bubblePlotXColumn);
        const yCol = sanitizeName(bubblePlotYColumn);
        const condExpr = [
          `${xCol} >= ${xMin}`,
          `${xCol} < ${xMax}`,
          `${yCol} >= ${yMin}`,
          `${yCol} < ${yMax}`
        ].join(' AND ');
        return {condExpr, count: item.count};
      });
      return {name: breakdown, x, y, customdata};
    }).value();

    dispatch(bubblePlotSlice.actions.fetchSuccess(grouped));
  } catch (err) {
    console.error(err);
    dispatch(bubblePlotSlice.actions.fetchError(err.message));
  }
};