import { createSlice } from '@reduxjs/toolkit';
import { RENDER_TYPES } from '@/constants';
import jsonata from 'jsonata';
import _ from 'lodash';




export const matchDerivedColumns = (meta) => {
  // TODO: this work will be done by AI in the future
  if ('image_url' in meta && 'bbox' in meta && 'category_name' in meta) {
    return [{
      name: 'bbox_url',
      renderType: RENDER_TYPES.IFRAME,
      baseUrl: './viz-bbox.html',
      params: {
        image: 'image_url',
        bboxes: '[{"bbox": bbox, "label": category_name}]',
        autorange: '"true"',
      },
    }]
  } else {
    return [];
  }
}

const derivedColumnsSlice = createSlice({
  name: 'derivedColumns',
  initialState: [],
  reducers: {
    addDerivedColumn: (state, action) => {
      state.push(action.payload);
    },
    removeDerivedColumn: (state, action) => {
      const name = action.payload;
      return state.filter(column => column.name !== name);
    },


  },
});

export const toFullUrl = ({baseUrl, params}) => {
  return baseUrl + '?' + _.map(params, (v, k) => `${k}=${encodeURIComponent(v)}`).join('&');
}

const evaluateExpr = async ({expr, singleRowData}) => {
  const result = await jsonata(expr).evaluate(singleRowData);
  if (typeof result === 'object') {
      return JSON.stringify(result);
  } else {
      return result;
  }
}

export const evaluateDictExpressions = async ({params, singleRowData}) => {
  const keys = _.keys(params);
  const promises = _.map(keys, (key) => evaluateExpr({expr: params[key], singleRowData}));
  const results = await Promise.all(promises);
  return _.zipObject(keys, results);
}

export const evaluateAllExpressionsForAllRows = async ({derivedColumns, rowData}) => {
  for (const row of rowData) {
    for (const col of derivedColumns) {
      const params = col.params;
      const evaluatedParams = await evaluateDictExpressions({params, singleRowData: row});
      const fullUrl = toFullUrl({baseUrl: col.baseUrl, params: evaluatedParams});
      row[col.name] = fullUrl;
    }
  }
  return rowData;
}


export const {
  addDerivedColumn,
  removeDerivedColumn,
} = derivedColumnsSlice.actions;

export default derivedColumnsSlice.reducer;
