import { createSlice } from '@reduxjs/toolkit';
import { lsApi } from '@/utils/api';
import path from 'path-browserify';
import _ from 'lodash';

const defaultState = {
  treeData: [],
  viewingItemId: '',
  viewFolderAs: {},
  expandedItems: []
};

export function pathJoin(rootFolder, relPath) {
  if (rootFolder.startsWith('s3://')) {
    return 's3://' + path.join(rootFolder.slice(5), relPath);
  } else {
    return path.join(rootFolder, relPath);
  }
}

export function pathBasename(fullPath) {
  if (fullPath.startsWith('s3://') && !fullPath.slice(5).includes('/')) {
    return fullPath;
  } else {
    return path.basename(fullPath);
  }
}

export function pathDirname(fullPath) {
  if (fullPath.startsWith('s3://')) {
    const dirname = path.dirname(fullPath.slice(5));
    return dirname === '.' ? '.' : 's3://' + dirname;
  } else {
    return path.dirname(fullPath);
  }
}

export function locateItem(treeNodeList, relPath) {
  const nextPath = relPath.split('/')[0];
  const nextNode = treeNodeList.find(item => item.id.split('/').pop() === nextPath);
  if (!relPath.includes('/')) {
      return nextNode;
  } else {
      return locateItem(nextNode.children, relPath.slice(nextPath.length + 1));
  }
}



function idLoading(relPath) {
  return `loading-${relPath}`;
}

const folderBrowserSlice = createSlice({
  name: 'folderBrowser',
  initialState: defaultState,
  reducers: {
    setViewingItemId: (state, action) => {
      state.viewingItemId = action.payload;
    },
    resetAll: (state) => {
      Object.keys(defaultState).forEach(key => {
        state[key] = defaultState[key];
      });
    },
    setExpandedItems: (state, action) => {
      state.expandedItems = action.payload;
    },
    toggleItemExpansion: (state, action) => {
      const itemId = action.payload;

      state.expandedItems = state.expandedItems.includes(itemId)
        ? state.expandedItems.filter(id => id !== itemId)
        : [...state.expandedItems, itemId];
    },
    setViewFolderAs: (state, action) => {
      const { id, viewAs } = action.payload;
      state.viewFolderAs[id] = viewAs;
    },
    updateChildrenSuccess: (state, action) => {
      const { itemId, children } = action.payload;
      const formatedChildren = children.map(c => ({
        id: pathJoin(itemId, c.name),
        label: c.name,
        secondaryLabel: c.isDir ? 'Folder' : 'File',
        ..._.pick(c, ['lastModified', 'isDir', 'size']),
        children: c.isDir ? [{id: idLoading(pathJoin(itemId, c.name)), label: 'Loading...'}] : [],
      }));

      if (itemId === '') {
        state.treeData = formatedChildren;
      } else {
        const item = locateItem(state.treeData, itemId);
        item.children = formatedChildren;
      }
    }
  },
});

// Async action creator
export const updateChildren = (itemId, force=false) => async (dispatch, getState) => {
  const state = getState();
  const rootFolder = state.settings.folderBrowserRootFolder;

  async function impl() {
    const parentPath = pathJoin(rootFolder, itemId);
    const showHidden = state.settings.folderBrowserShowHiddenFiles;
    const limit = state.settings.folderBrowserListLimit;
    const children = await lsApi({ parentPath, showHidden, limit, dispatch });
    dispatch(folderBrowserSlice.actions.updateChildrenSuccess({ itemId, children }));
  }

  if (!rootFolder) {
    return;
  } else if (itemId === '') {
    await impl();
  } else {
    const item = locateItem(state.folderBrowser.treeData, itemId);
    if (force || (item &&item.children && item.children.length === 1 && item.children[0].id === idLoading(itemId))) {
      await impl();
    }
  }
};

export const { setViewingItemId, setViewFolderAs, setExpandedItems, toggleItemExpansion } = folderBrowserSlice.actions;



export default folderBrowserSlice.reducer;
