import { createSlice } from '@reduxjs/toolkit';
import { format } from 'sql-formatter';




const sqlSlice = createSlice({
  name: 'sql',
  initialState: {},
  reducers: {
    updateSqlRecord: (state, action) => {
      const { key, records } = action.payload;
      if (!(key in state)) {
        state[key] = {};
      }
      Object.entries(records).forEach(([k, v]) => {
        if (k === 'query') {
          try {
            state[key].query = format(v, { language: 'duckdb'}) + '\n';
          } catch (error) {
            state[key].query = v + '\n';
            console.warn(error);
          }
          state[key].lastUpdated = Date.now();
        } else {
          state[key][k] = v;
        }
      });
    },

  },
});


export const { updateSqlRecord } = sqlSlice.actions;
export default sqlSlice.reducer;
