import { createSlice } from '@reduxjs/toolkit';
import { map, pick } from 'lodash';
import { filteredColumnStatsSlice } from '@/store/dataSlice';
import { rowDataSlice } from '@/store/dataSlice';

const initialState = {
    columnDefs: [],
    sorting: [],
    filters: {},
    baselineFilters: {}
};


export const agColDefsSlice = createSlice({
  name: 'agColDefs',
  initialState: initialState,
  reducers: {
    setColumnDefs: (state, action) => {
      state.columnDefs = action.payload;
    },
    setSorting: (state, action) => {
      state.sorting = action.payload;
    },
    updateDef: (state, action) => {
      const { field, name, value } = action.payload;
      const colDef = state.columnDefs.find(col => col.field === field);
      if (colDef) {
        colDef[name] = value;
      }
    },
    setFilters: (state, action) => {
      state.filters = action.payload;
    },
    setFilterForColumn: (state, action) => {
      const { field, filter } = action.payload;
      state.filters[field] = filter;
    },
    clearFilterForColumn: (state, action) => {
      const { field  } = action.payload;
      delete state.filters[field];
    },

    setBaselineFilters: (state) => {
      state.baselineFilters = state.filters;
    },
  },
});

export const pickAgColDefs = (api) => {
    return map(api.getColumnDefs(), (col) =>
        pick(col, [
          'hide', 'pinned',
          'field',
        ])
      );
}

const refreshDataAfterFilterChange = (dispatch) => {
  dispatch(filteredColumnStatsSlice.actions.refreshAllData());
  dispatch(rowDataSlice.actions.setShouldRefresh());

}

export const setColumnFilterAndRefreshData = (dispatch, columnName, filter) => {
  dispatch(setFilterForColumn({field: columnName, filter}));
  refreshDataAfterFilterChange(dispatch);
}

export const removeColumnFilterAndRefreshData = (dispatch, columnName) => {
  dispatch(agColDefsSlice.actions.clearFilterForColumn({field: columnName}));
  refreshDataAfterFilterChange(dispatch);
};

export const { setColumnDefs, updateDef, setFilters, setSorting, setFilterForColumn, clearFilterForColumn, setBaselineFilters } = agColDefsSlice.actions;

