import { createSlice } from '@reduxjs/toolkit';


export const initialState = {
  data: null,
  loading: false,
  shouldRefresh: false,
  error: null,
};

export const createDataSlice = (sliceName) => createSlice({
  name: sliceName,
  initialState,
  reducers: {
    setData: (state, action) => {
      state.data = action.payload;
    },
    setShouldRefresh: (state, action) => {
      state.shouldRefresh = true;
    },
    fetchStart: (state) => {
      state.loading = true;
      state.error = null;
      state.shouldRefresh = false;
    },
    fetchSuccess: (state, action) => {
      state.data = action.payload;
      state.loading = false;
      state.error = null;
      state.shouldRefresh = false;
    },
    fetchError: (state, action) => {
      state.error = action.payload;
      state.loading = false;
      state.shouldRefresh = false;
    }
  }
});


const validateName = (state, action) => {
  const name = action.payload?.name;
  if (!name) alert(`Name is required in ${JSON.stringify(action)}`);
  if (!state[name]) {
    state[name] = {};
  }
  return name;
}

export const createSubnamedDataSlice = (sliceName) => createSlice({
  name: sliceName,
  initialState: {},
  reducers: {
    resetAllData: (state) => {
      Object.keys(state).forEach(key => {
        state[key] = {data: null, loading: false, error: null, shouldRefresh: false};
      });
    },
    refreshAllData: (state) => {
      Object.keys(state).forEach(key => {
        state[key].shouldRefresh = true;
      });
    },
    resetData: (state, action) => {
      const name = validateName(state, action);
      state[name] = null;
    },
    setShouldRefresh: (state, action) => {
      const name = validateName(state, action);
      state[name].shouldRefresh = true;
    },
    fetchSuccess: (state, action) => {
      const name = validateName(state, action);
      state[name] = {data: action.payload, loading: false, error: null, shouldRefresh: false};
    },
    fetchStart: (state, action) => {
      const name = validateName(state, action);
      state[name].loading = true;
      state[name].shouldRefresh = false;
    },
    fetchError: (state, action) => {
      const name = validateName(state, action);
      state[name] = {data: null, loading: false, error: action.payload.error, shouldRefresh: false};
    },
  },
});

export const columnMetaSlice = createDataSlice('columnMeta');
export const baselineColumnStatsSlice = createSubnamedDataSlice('baselineColumnStats');

export const filteredColumnStatsSlice = createSubnamedDataSlice('filteredColumnStats');
export const cardinalitySlice = createSubnamedDataSlice('cardinality');

export const rowDataSlice = createDataSlice('rowData');

export const initSettingsSlice = createDataSlice('initSettings');