import _ from 'lodash';
import { updateSqlRecord } from '@/store/sqlSlice';
import { API_PREFIX } from '@/utils/urls';
import { showToast } from '@/store/toastSlice';
import { needProxy } from '@/utils/urls';

export const postApi = async ({url, data = {}, dispatch = null}) => {
    const res = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data)
      });

    if (!res.ok) {
      const errorMessage = 'Failed to fetch data from server';
      if (dispatch) {
        dispatch(showToast({
          message: errorMessage,
          severity: 'error'
        }));
      }
      throw new Error(errorMessage);
    }
    const json = await res.json();

    return json;
}

export const runQuery = async ({dispatch, key, query, transformResult = 'listOfDicts'}) => {
  dispatch(updateSqlRecord({key, records: {query, status: 'running', data: null, runtime: null}}));
  try {
    const response = await postApi({
      url: `${API_PREFIX}/query`,
      data: { query },
      dispatch,
    });

    if (response.status === 'error') {
      console.error(response.error);
      // Show toast notification for the error
      dispatch(showToast({
        message: response.error || 'An error occurred while running the query',
        severity: 'error'
      }));
      dispatch(updateSqlRecord({key, records: {
        error: response.error || 'An error occurred while running the query',
        status: 'error',
      }}));
      throw new Error(response.error || 'An error occurred while running the query');
    }

    dispatch(updateSqlRecord({key, records: {
      runtime: response.runtime,
      error: response.error,
      status: response.status,
      data: response.rows.map(row => _.zipObject(response.column_names, row)),
    }}));

    switch (transformResult) {
      case 'listOfDicts':
        const rowData = response.rows.map((row, index) => ({
          ..._.zipObject(response.column_names, row), rowIndex: index
        }));
        return rowData;

      case 'dictOfLists':
        return _.zipObject(response.column_names, _.zip(...response.rows));
      case 'none':
        return _.pick(response, ['column_names', 'rows']);
      default:
        throw new Error(`Invalid way of transforming result: ${transformResult}`);
    }
  } catch (error) {
    console.error(error);
    dispatch(updateSqlRecord({key, records: {
      error: error.message,
      status: 'error',
    }}));
    throw error;
  }
}

export const fetchParquetMeta = async ({dispatch, dataFile, sqlKey}) => {
  const query = `WITH meta AS (
    SELECT
        path_in_schema AS column_name,
        SUM(num_values) AS cntAll,
        SUM(stats_null_count) AS cntNull,
        MIN(stats_min_value) AS min,
        MAX(stats_max_value) AS max
    FROM parquet_metadata('${dataFile}')
    GROUP BY path_in_schema
  )
  SELECT column_name, column_type, cntAll, cntNull, min, max
  FROM (DESCRIBE SELECT * FROM read_parquet ('${dataFile}'))
      LEFT JOIN meta USING (column_name)
  `;
  return await runQuery({dispatch, key: sqlKey, query: query});
}

export const previewCsv = async ({dispatch, dataFile, sqlKey}) => {
  const query = `SELECT * FROM read_csv ('${dataFile}') LIMIT 10`;
  return await runQuery({dispatch, key: sqlKey, query: query});
}

export const lsApi = async ({parentPath, showHidden=false, limit=100, dispatch}) => {
  const response = await fetch(`${API_PREFIX}/ls?path=${encodeURIComponent(parentPath)}&show_hidden=${showHidden}&limit=${limit}`);
  if (!response.ok) {
    const errorMessage = 'Failed to fetch directory listing';
    if (dispatch) {
      dispatch(showToast({
        message: errorMessage,
        severity: 'error'
      }));
    }
    throw new Error(errorMessage);
  }
  const json = await response.json();

  return json;
}


export const batchProxyUrlApi = async ({urls}) => {
  if (_.every(urls, url => needProxy(url))) {
    const response = await postApi({
      url: `${API_PREFIX}/s3-proxy`,
      data: { urls },
    });
    return response;
  } else {
    return urls;
  }
}

