import { createSlice } from '@reduxjs/toolkit';

const viewingSlice = createSlice({
  name: 'viewing',
  initialState: {
    justClickedColumn: null,
    justClickedRow: null,
    pagination: {
      pageSize: 10,
      pageNumber: 0,
    },
    rowDataSource: null,
    sampleCondition: 'True',
    heatmapColumnSort: {},
    visibleColumns: [],
  },
  reducers: {
    setJustClickedColumn: (state, action) => {
      state.justClickedColumn = action.payload;
    },
    setJustClickedRow: (state, action) => {
      state.justClickedRow = action.payload;
    },
    updatePagination: (state, action) => {
      state.pagination = {...state.pagination, ...action.payload};
    },
    updateCountBaseline: (state, action) => {
      state.pagination.countBaseline = action.payload;
    },
    updateCountFiltered: (state, action) => {
      state.pagination.countFiltered = action.payload;
    },
    setRowDataSource: (state, action) => {
      state.rowDataSource = action.payload;
    },
    setSampleCondition: (state, action) => {
      state.sampleCondition = action.payload;
    },
    toggleHeatmapColumnSort: (state, action) => {
      const newByRow = action.payload;
      const orderList = ['desc', 'asc', 'none'];
      const { byRow, order } = state.heatmapColumnSort;
      if (byRow === newByRow) {
        state.heatmapColumnSort = {byRow: newByRow, order: orderList[(orderList.indexOf(order) + 1) % orderList.length]};
      } else {
        state.heatmapColumnSort = {byRow: newByRow, order: orderList[0]};
      }
    },
    setFreeFormQuery: (state, action) => {
      state.freeFormQuery = action.payload;
    },
    setVisibleColumns: (state, action) => {
      state.visibleColumns = action.payload;
    },
  },
});



export const setJustClickedColumn = ({dispatch, columnName}) => {
  const action = viewingSlice.actions.setJustClickedColumn;
  dispatch(action(columnName));
  setTimeout(() => {
    dispatch(action(null));
  }, 1000);
}



export const {
  updatePagination, setJustClickedRow, setRowDataSource, setSampleCondition,
  toggleHeatmapColumnSort,
  setFreeFormQuery,
  updateCountBaseline,
  updateCountFiltered,
  setVisibleColumns,
} = viewingSlice.actions;

export default viewingSlice.reducer;
